const { registerBlockType } = wp.blocks;
const { withAPIData } = wp.components;
const blockStyle = { backgroundColor: '#900', color: '#fff', padding: '20px' };

const getCarbonFields = () => ({ options: '/carbon-fields/v1/options' });

registerBlockType('colbycomms/whos-coming', {
  title: "Who's Coming",
  icon: 'universal-access-alt',
  category: 'layout',
  edit: withAPIData(getCarbonFields)(props => {
    console.log('hi');
    console.log(props);
    return <p style={blockStyle}>Hello editor.</p>;
  }),
  save: () => <p style={blockStyle}>Hello saved content.</p>,
});



